	import financial from './financial.vue'
	import operating from './operating.vue'
	import vip from './vip.vue'
	import order from './order.vue'
    export default {
        components: {
            // mediaItem
			financial,
			operating,
			vip,
			order
        },
        data() {
            return {
                newsList: [],
                cacheTab: [],
                tabIndex: 0,
                tabBars: [{
                    name: '工作台',
                    id: 'gzt',
					index:0,
					iconPath:'../../static/img/pic/tab/icon1.png',
					selecctedIconPath:'../../static/img/pic/tab/icon1_se.png'
                }, {
                    name: '会员',
                    id: 'hy',
					index:1,
					iconPath:'../../static/img/pic/tab/icon4.png',
					selecctedIconPath:'../../static/img/pic/tab/icon4_se.png'
                }, {
                    name: '订单',
                    id: 'dd',
					index:2,
					iconPath:'../../static/img/pic/tab/icon3.png',
					selecctedIconPath:'../../static/img/pic/tab/icon3_se.png'
                }, {
                    name: '财务',
                    id: 'cw',
					index:3,
					iconPath:'../../static/img/pic/tab/icon2.png',
					selecctedIconPath:'../../static/img/pic/tab/icon2_se.png'
                }, {
                    name: '我的',
                    id: 'wd',
					index:4,
					iconPath:'../../static/img/pic/tab/icon5.png',
					selecctedIconPath:'../../static/img/pic/tab/icon5_se.png'
                }],
                scrollInto: "",
                showTips: false,
                navigateFlag: false,
                pulling: false,
                refreshIcon: "data:image/png;base64,"
            }
        },
        onLoad() {
			var that=this
            setTimeout(()=>{
				uni.setNavigationBarTitle({
				    title: that.toggleTitle(0)
				});
              // this.tabBars.forEach((tabBar) => {
              //     this.newsList.push({
              //         data: [],
              //         isLoading: false,
              //         refreshText: "",
              //         loadingText: '加载更多...'
              //     });
              // });
            },350)
        },
        methods: {
            close(index1, index2) {
                uni.showModal({
                    content: '是否删除本条信息？',
                    success: (res) => {
                        if (res.confirm) {
                            this.newsList[index1].data.splice(index2, 1);
                        }
                    }
                })
            },
            loadMore(e) {
            },
			toggletab(e){
				 this.switchTab(Number(e));
			},
            ontabtap(e) {
                let index = e.target.dataset.current || e.currentTarget.dataset.current;
				if(index==4){
					uni.switchTab({
						url:'/pages/tabBar/mine'
					})
					return ;
				}
                this.switchTab(Number(index));
            },
            ontabchange(e) {
                let index = e.target.current || e.detail.current;
                this.switchTab(index);
            },
            switchTab(index) {
                // if (this.newsList[index].data.length === 0) {
                //     this.getList(index);
                // }
				var that=this
                if (this.tabIndex === index) {
                    return;
                }

                // 缓存 tabId
                // if (this.newsList[this.tabIndex].data.length > 5) {
                //     let isExist = this.cacheTab.indexOf(this.tabIndex);
                //     if (isExist < 0) {
                //         this.cacheTab.push(this.tabIndex);
                //         //console.log("cache index:: " + this.tabIndex);
                //     }
                // }
				uni.setNavigationBarTitle({
				    title: that.toggleTitle(index)
				});
                this.tabIndex = index;
				// console.log(this.$refs['com'+index])
				this.$refs['com'+index].init()
                this.scrollInto = this.tabBars[index].id;

                // 释放 tabId
                if (this.cacheTab.length > 5) {
                    let cacheIndex = this.cacheTab[0];
                    this.clearTabData(cacheIndex);
                    this.cacheTab.splice(0, 1);
                    //console.log("remove cache index:: " + cacheIndex);
                }
            },
            clearTabData(e) {
                this.newsList[e].data.length = 0;
                this.newsList[e].loadingText = "加载更多...";
            },
			toggleTitle(index){
				switch(index){
					case 0:
					return '工作台';
					case 1:
					return '会员';
					case 2:
					return '订单';
					case 3:
					return '财务';
					case 4:
					return '我的';
				}
			},
            refreshData() {},
            onrefresh(e) {
                var tab = this.newsList[this.tabIndex];
                if (!tab.refreshFlag) {
                    return;
                }
                tab.refreshing = true;
                tab.refreshText = "正在刷新...";

                setTimeout(() => {
                    this.refreshData();
                    this.pulling = true;
                    tab.refreshing = false;
					tab.refreshFlag = false;
                    tab.refreshText = "已刷新";
                    setTimeout(() => { // TODO fix ios和Android 动画时间相反问题
                        this.pulling = false;
                    }, 500);
                }, 2000);
            },
            onpullingdown(e) {
                var tab = this.newsList[this.tabIndex];
                if (tab.refreshing || this.pulling) {
                    return;
                }
                if (Math.abs(e.pullingDistance) > Math.abs(e.viewHeight)) {
                    tab.refreshFlag = true;
                    tab.refreshText = "释放立即刷新";
                } else {
                    tab.refreshFlag = false;
                    tab.refreshText = "下拉可以刷新";
                }
            },
            newGuid() {
                let s4 = function() {
                    return (65536 * (1 + Math.random()) | 0).toString(16).substring(1);
                }
                return (s4() + s4() + "-" + s4() + "-4" + s4().substr(0, 3) + "-" + s4() + "-" + s4() + s4() + s4()).toUpperCase();
            }
        }
    }
